.iqvia-profile-setup-conatiner {
  margin-top: 140px;
  margin-right: 16px;
  margin-left: 16px;
  max-width: 500px;
}
.iqvia-profile-setup-conatiner .ant-upload {
  margin: 0px;
}
.iqvia-profile-setup-conatiner .ant-upload.ant-upload-drag {
  border: none;
  height: 150px;
  background: transparent;
  margin-bottom: 40px;
}
.iqvia-profile-setup-conatiner .ant-upload.ant-upload-drag img {
  margin: 0 auto;
}
.iqvia-profile-setup-conatiner .ant-upload.ant-upload-drag-hover {
  border: none;
}
.iqvia-profile-setup-conatiner .ant-upload.ant-upload-drag.ant-upload-drag-hover:not(.ant-upload-disabled) {
  border: none;
}
.iqvia-profile-setup-conatiner .setup-profile-footer .upload-btn {
  height: 45px;
  border-radius: 4px;
  background-color: #32b9d1;
  color: #ffffff;
}
.iqvia-profile-setup-conatiner .setup-profile-footer.hide-desktop {
  border-top: solid 1px #d4d7d9;
  position: fixed;
  bottom: 0px;
  right: 16px;
  width: 100%;
}
.iqvia-profile-setup-conatiner .setup-profile-footer.hide-desktop > div {
  float: right;
  margin: 14px 0;
}
@media (min-width: 1025px) {
  .iqvia-profile-setup-conatiner .msg {
    top: 60px;
  }
}
@media (max-width: 1024px) {
  .msg {
    bottom: 74px;
  }
}
