.programDetailsBody {
  height: fit-content;
  min-height: 100%;
  background-color: rgba(212, 215, 217, 0.1);
}
.program-success-msg {
  top: 136px;
  bottom: 0px;
}
.listBody {
  height: calc(100vh - 72px);
  overflow: auto;
  padding-bottom: 36px;
}
.searchBoxContainer-360-40 {
  width: 360px;
  height: 40px;
}
.searchBoxContainer-360-40 .ant-input-affix-wrapper {
  height: 100% !important;
  width: 100% !important;
}
.searchBoxContainer-360-40 .ant-input {
  padding-top: 0px !important;
  padding-bottom: 0px !important;
  font-size: 14px !important;
  height: 100%;
  width: 100%;
  border: solid 1px #8fa3aa;
}
.searchBoxContainer-360-40 .ant-input:hover,
.searchBoxContainer-360-40 .ant-input:active,
.searchBoxContainer-360-40 .ant-input:focus {
  border: solid 1px #8fa3aa !important;
  box-shadow: 0px 0px 0px 0px;
}
.grayDropdownContainer-220-40 .ant-select {
  width: 220px !important;
  height: 40px !important;
}
.grayDropdownContainer-220-40 .ant-select .ant-select-selection {
  background-color: rgba(212, 215, 217, 0.3) !important;
  border: 0px !important;
  box-shadow: none;
  border: 0;
}
.grayDropdownContainer-220-40 .ant-select .ant-select-selection--single {
  height: 40px !important;
}
.grayDropdownContainer-220-40 .ant-select .ant-select-selection--single .ant-select-selection__rendered .ant-select-selection-selected-value {
  font-family: "AvenirNext-Medium" !important;
  font-size: 14px !important;
}
