}
.createSurvey .patient-pane {
  height: 100%;
  background-color: rgba(212, 215, 217, 0.1);
  margin-bottom: 150px;
}
.createSurvey .patient-pane .patients-selection {
  height: 100%;
  margin: 0 4px 0 16px;
  overflow-y: auto;
}
.createSurvey .patient-pane .select-program {
