.leftPanelBox {
  width: 360px;
  height: calc(100vh - calc(72px + 64px));
  background: #ffffff;
  padding-left: 24px;
  padding-right: 40px;
}
.product {
  width: 115px;
  height: 32px;
  border-radius: 20px;
  background-color: rgba(204, 237, 243, 0.5);
  margin-right: 8px;
  margin-bottom: 8px;
}
.product span:nth-child(1) {
  margin-right: 12px;
}
.product span:nth-child(2) {
  text-align: center;
  font-size: 12px;
  color: #005587;
  font-family: "AvenirNext-Medium";
}
