.patient-response {
  margin-top: 64px;
}
.patient-response .response-header {
  padding: 16px;
}
.patient-response .progress-bar-div {
  max-width: 300px;
}
.patient-response .change-btn {
  background: rgba(212, 215, 217, 0.15);
  border: none;
}
.patient-response .survey-questions {
  background-color: rgba(212, 215, 217, 0.1);
}
.patient-response .patient-survey-question {
  padding-top: 80px;
  margin-left: 356px;
  margin-right: 356px;
}
.patient-response .surveyDescription {
  margin-bottom: 72px;
}
.patient-response .radio-group-tab {
  display: flex;
}
.patient-response .ant-radio-button-wrapper {
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.patient-response .ant-radio-button-wrapper > span {
  font-size: 14px;
  color: #2b3a42;
  font-family: "AvenirNext-Medium";
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.patient-response .ant-radio-button-wrapper-checked > span {
  color: #ffffff;
}
.patient-response .change-btn.ant-btn[disabled] {
  background-color: rgba(212, 215, 217, 0.15);
}
.patient-response .disable-question {
  opacity: 0.5;
}
.patient-response .survey-response-header {
  position: fixed;
  z-index: 111;
  background: white;
  width: 100%;
  max-height: 72px;
}
.patient-response .response-header-of-patient {
  z-index: 1111;
}
.patient-response .response-header-of-patient .survey-response-header .response-header .prev-next-btn .links .change-btn.ant-btn[disabled] {
  background-color: rgba(212, 215, 217, 0.15) !important;
}
/* width */
.survey-completed {
  height: 100%;
  display: flex;
  flex-grow: 12;
  align-items: center;
  justify-content: center;
}
