.twilio-chat-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  height: 90%;
  width: 100%;
}
.twilio-chat-container .twilio-chat-body {
  color: white;
  width: 100%;
  overflow-y: scroll;
  overflow-x: hidden;
}
.twilio-chat-container .twilio-chat-body .chat-messages {
  display: flex;
  height: auto;
  padding: 10px 24px;
  width: 100%;
  flex-direction: column;
}
.twilio-chat-container .twilio-chat-body .chat-messages.end {
  justify-content: flex-end;
}
.twilio-chat-container .twilio-chat-body .chat-avatar {
  display: flex;
  height: auto;
}
.twilio-chat-container .twilio-chat-body .chat-avatar.left {
  margin-left: 8px;
}
.twilio-chat-container .twilio-chat-body .chat-avatar.right {
  margin-right: 8px;
}
.twilio-chat-container .twilio-chat-body .chat-text {
  display: flex;
  width: auto;
  height: auto;
  word-break: break-word;
  align-self: flex-start;
}
.twilio-chat-container .twilio-chat-body .chat-text.end {
  align-self: flex-end;
  text-align: end;
}
.twilio-chat-container .twilio-chat-body .chat-time {
  display: flex;
  width: auto;
  height: auto;
  word-break: break-word;
  align-self: flex-end;
  font-size: 12px;
  color: #aab0b3;
  padding: 2px;
}
.twilio-chat-container .twilio-chat-body .chat-time.start {
  align-self: flex-start;
  margin-left: 32px;
}
.twilio-chat-container .twilio-chat-body .twilio-avatar .ant-avatar.ant-avatar-circle {
  width: 24px;
  height: 24px;
  left: 0;
  border-radius: 15px;
  top: 0;
  margin-right: 10px;
}
.twilio-chat-footer {
  display: flex;
  height: 10%;
  width: 100%;
  align-items: center;
}
.twilio-chat-footer .footer-left {
  display: flex;
  height: 100%;
  width: 20%;
  justify-content: center;
  align-items: center;
}
.twilio-chat-footer .footer-left .back-image {
  cursor: pointer;
}
.twilio-chat-footer .footer-right {
  display: flex;
  height: 100%;
  width: 80%;
  align-items: center;
}
.twilio-chat-footer .footer-right .chat-form {
  display: flex;
}
.twilio-chat-footer .footer-right .chat-form .form-input {
  display: flex;
}
.twilio-chat-footer .footer-right .chat-form .form-button {
  display: flex;
}
