.article.details .article-header {
  position: fixed;
  top: 64px;
  left: 0;
  right: 0;
  background-color: #ffffff;
  padding: 0 24px;
  height: 72px;
  border-bottom: solid 1px #d4d7d9;
}
.article.details .article-header .back {
  height: 24px;
  width: 24px;
}
.article.details .article-header .more,
.article.details .article-header .usr {
  background-color: #f4f4f4;
  width: 32px;
  height: 32px;
  border-radius: 16px;
}
.article.details .content-wrapper {
  min-height: 100vh;
  padding: 136px 0 72px 0;
  background-color: rgba(212, 215, 217, 0.1);
}
.article.details .content-wrapper .content {
  width: 972px;
  margin: 36px auto;
}
.article.details .content-wrapper .content .like,
.article.details .content-wrapper .content .share {
  height: 18px;
  width: 18px;
}
.article.details .content-wrapper .content .article-title {
  border-bottom: solid 1px #d4d7d9;
}
