.navBarContainer {
  height: calc(72px);
  background-color: #ffffff;
  box-shadow: 0px 0px 1px 0px gray;
}
.ico-Back {
  width: 24px;
  height: 24px;
  object-fit: contain;
}
.ActiveState {
  width: 50px;
  height: 24px;
  border-radius: 12px;
  background-color: #e3f3df;
  display: flex;
  justify-content: center;
  align-items: center;
}
.ExpiredState {
  width: 50px;
  height: 24px;
  border-radius: 12px;
  background-color: #f2f3f3;
  display: flex;
  justify-content: center;
  align-items: center;
}
.navbarMenuContainer .ant-menu-horizontal {
  border-bottom: 4px !important;
}
.navbarMenuContainer .ant-menu .ant-menu-item {
  padding: 0px;
}
.navbarMenuContainer .ant-menu .ant-menu-item a {
  width: 89px;
  height: 24px;
  font-family: "AvenirNext-Medium";
  font-size: 14px;
  font-weight: 500;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.71;
  letter-spacing: normal;
  text-align: center;
  margin-bottom: 12px;
}
.navbarMenuContainer .ant-menu .ant-menu-item:hover {
  border-bottom: 4px solid #2b3a42 !important;
}
.navbarMenuContainer .ant-menu .ant-menu-item-selected {
  border-bottom: 4px solid #2b3a42 !important;
}
.navbarMenuContainer .ant-menu .ant-menu-item-selected a {
  width: 89px;
  height: 24px;
  font-family: "AvenirNext-Medium";
  font-size: 14px;
  font-weight: 500;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.71;
  letter-spacing: normal;
  text-align: center;
}
.orange-outlined-button.ant-btn {
  color: #ff530d;
  border: solid 1px #ff530d;
}
.orange-outlined-button.ant-btn:focus {
  color: #ff530d;
  border: solid 1px #ff530d;
}
.orange-outlined-button.ant-btn:hover {
  color: #ff530d;
  border: solid 1px #ff530d;
}
.dischargeModal.ant-modal {
  height: 100%;
  position: absolute;
  top: 0px;
  right: 0px;
  padding-bottom: 0px;
}
.dischargeModal .ant-modal-close {
  display: none;
}
.dischargeModal .ant-modal-content {
  height: 100%;
}
.dischargeModal .ant-modal-footer {
  position: absolute;
  bottom: 0px;
  width: 100%;
}
.modal-warining-box {
  position: absolute;
  bottom: 93px;
}
.blue-outlined-button.ant-btn {
  color: #39b9d2;
  border: solid 1px #39b9d2;
}
.blue-outlined-button.ant-btn:focus {
  color: #39b9d2;
  border: solid 1px #39b9d2;
}
.blue-outlined-button.ant-btn:hover {
  color: #39b9d2;
  border: solid 1px #39b9d2;
}
.orange-primary-button.ant-btn {
  color: #ffffff;
  border: solid 1px #ff530d;
  background: #ff530d;
}
.orange-primary-button.ant-btn:focus {
  color: #ffffff;
  border: solid 1px #ff530d;
  background: #ff530d;
}
.orange-primary-button.ant-btn:hover {
  color: #ffffff;
  border: solid 1px #ff530d;
  background: #ff530d;
}
.min-width-318 {
  min-width: 318px;
}
