.survey {
  margin-left: 240px;
  margin-right: 240px;
  min-height: 100%;
  display: flex;
  top: 64px;
  position: relative;
  margin: 0 auto;
  flex-direction: column;
}
.survey .survey-header {
  margin-top: 100px;
}
.survey .survey-input {
  max-width: 200px;
  margin-right: 8px;
}
.survey .survey-input.ant-input-affix-wrapper .ant-input {
  height: 40px;
  padding-left: 40px;
}
.survey .sortBy {
  height: 40px;
  background: rgba(212, 215, 217, 0.3);
  border-radius: 4px;
}
.survey .sortBy .ant-select .ant-select-selection {
  height: 40px;
  width: 130px;
  border: 0;
  background: rgba(212, 215, 217, 0);
  margin-bottom: 12px;
}
.survey .sortBy .ant-select-focused .ant-select-selection {
  box-shadow: none;
  border: 0;
}
.survey .sortBy .ant-select-open .ant-select-selection {
  box-shadow: none;
  border: 0;
}
.survey .sortBy .ant-select .ant-select-selection .ant-select-selection__rendered .ant-select-selection-selected-value {
  color: #2b3a42;
  font-family: "AvenirNext-Medium";
}
.survey .sortBy .ant-select .ant-select-selection .ant-select-selection__rendered {
  margin: 0;
  padding: 5px 10px;
}
