.dashboard-home {
  min-height: 100%;
  display: flex;
  width: 80%;
  height: auto;
  margin-top: 64px;
  justify-content: space-evenly;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
}
.dashboard-home .appointment-section {
  display: flex;
  width: 50%;
  height: 100%;
  flex-direction: column;
  padding-right: 12px;
}
.dashboard-home .reminder-section {
  display: flex;
  width: 50%;
  flex-direction: column;
  padding-left: 12px;
  height: 100%;
}
.dashboard-bottom {
  min-height: 100%;
  display: flex;
  width: 80%;
  height: auto;
  margin-top: 10px;
  justify-content: space-evenly;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  padding-bottom: 120px;
}
.dashboard-bottom .appointment-section {
  display: flex;
  width: 50%;
  height: 100%;
  flex-direction: column;
  padding-right: 12px;
}
.dashboard-bottom .reminder-section {
  display: flex;
  width: 50%;
  flex-direction: column;
  padding-left: 12px;
  height: 100%;
}
